.one{
    display: flex;
    height: 200px;
    width: 100%;
    background: rgb(134, 134, 134);
    padding-top: 20px;
    padding-left: 20px;
    gap: 20px;
    border-left: solid 1px black;

}

.sair1{
    position: absolute;
    right: 30px;
    top: 20px;
    background: white;
    cursor: pointer;
    padding: 20px 50px;
    box-shadow: 1px 1px 1px 1px black;
}

.sair1:hover{
    font-weight: bold;
    background: rgb(0, 0, 0);
    box-shadow: 1px 1px 1px 1px rgb(255, 255, 255);
    color: white;
}

.block{
    display: flex;
    flex-direction: column;
    height: 150px;
    width: 150px;
    background-color: rgb(255, 255, 255);

    border-radius: 10px;
    transition: all ease .5s;
    cursor: pointer;
}

.one:hover .block{
    margin-left: 150px;
    height: 160px;
    width: 160px;
    box-shadow: 1px 1px 1px 1px rgb(0, 0, 0);

}

.block:hover{
    background:rgb(219, 219, 219);
    color: white;
}

