  p {
    text-shadow: 0 0 15px #fff, 0 0 30px rgb(255 255 255 / 30%);
    color: #f8b700;
    font-weight: bold;
  }
  
  .blink{
    animation-name: blink;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
  
  @keyframes blink{
    0% {opacity: 0.2;}
    50% {opacity: 1;}
    100% {opacity: 0.2;}
  }


  .size{
    animation-name: blink;
    animation-duration: 5s;
    animation-iteration-count: infinite;
  }
  
  @keyframes size{
    0% {opacity: 0.2; font-size: 10px; color: black;}
    50% {opacity: 1; font-size: 20px; color: grey;}
    100% {opacity: 0.2; font-size: 30px;}
  }   
  
  input{
    height: 20px;
  }
  

  
  span{
    width: auto;
    border: solid #600000;
    border-radius: 5px;
    padding: 2px 8px 2px 8px;
    background: #600000;
    cursor: pointer;
    color: #f8b700;
  }
  
  .start{
    position: relative;
  }
  span:hover{
    border: solid #600000;
    font-weight: bold;
    text-shadow:0 0 15px #fff, 0 0 30px rgb(255 255 255 / 30%);
  }
  
  main{
    display: block;
    overflow: hidden;
    width: 200vw;
   /* height: calc(100vh - 120px);*/
   height: auto;

    transition:  ease .5s;
  }
  
  
  .previous{
    display: block;
    overflow: hidden;
    width: 200vw;
    height: calc(100vh - 120px);
    transition:  ease .5s;
    margin-left: 0vw;
  }
  
  .next{
    display: block;
    overflow: hidden;
    width: 200vw;
    height: calc(100vh - 120px);
    transition:  ease .5s;
    margin-left: -100vw;
  }
  
  
