  .modal{
    position: absolute;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: #000000ea;
    display: flex;
    justify-content: center;
    align-items: center;
  
  
    justify-content: center;
